import { useState, useEffect, useCallback } from "react";
import axios from "axios";

const useFetchDetail = (endpoint) => {
    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    const fetchData = useCallback(async () => {
        if (!endpoint) {
            console.warn("No endpoint provided to fetch data.");
            return; // Exit early if no endpoint is provided
        }

        setLoading(true);
        setError(null); // Reset error before fetching new data

        try {
            const response = await axios.get(endpoint);
            setData(response.data); // Set the fetched data
        } catch (err) {
            setError(err); // Capture and set the error
            console.error('Error fetching data:', err.message || err);
        } finally {
            setLoading(false); // Stop loading regardless of success or failure
        }
    }, [endpoint]);

    useEffect(() => {
        fetchData(); // Fetch data when component mounts or endpoint changes
    }, [fetchData]); // Dependency on fetchData to ensure it's updated

    return { data, loading, error }; // Return data, loading, and error states
};

export default useFetchDetail;
