import moment from 'moment';
import React from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';

const Card = ({ data, trending, index, media_type }) => {
    const imageURL = useSelector(state => state.movieoData.imageURL);
    const mediaType = data.media_type ?? media_type;

    return (
        <Link
            to={"/" + mediaType + "/" + data.id}
            className='w-full min-w-[230px] max-w-[230px] h-80 overflow-hidden block rounded relative hover:scale-105 transition-all'
        >
            <img
                src={data?.poster_path ? imageURL + data.poster_path : "/path-to-placeholder.jpg"}
                alt={data?.title || data?.name || "No Title Available"}
            />
            <div className='trend absolute top-4'>
                {trending && (
                    <div className='py-1 px-4 backdrop-blur-3xl rounded-r-full bg-black/60 overflow-hidden'>
                        #{index}Trending
                    </div>
                )}
            </div>
            <div className='absolute bottom-0 h-16 backdrop-blur-3xl w-full bg-black/50 p-2'>
                <h2 className='text-ellipsis line-clamp-1 text-lg font-semibold whiteText'>
                    {data.title || data.name}
                </h2>
                <div className='text-sm text-neutral-400 flex justify-between items-center'>
                    <p className='whiteText'>
                        {data.release_date ? moment(data.release_date).format("MMMM Do YYYY") : "No Release Date"}
                    </p>
                    <p className='px-1 bg-black rounded-full text-xs text-white whiteText'>
                        Rating: {data.vote_average ? Number(data.vote_average).toFixed(1) : "N/A"}
                    </p>
                </div>
            </div>
        </Link>
    );
};

export default Card;
