import React from 'react';
import { useParams } from 'react-router-dom';
import useFetchDetail from '../hooks/useFetchDetail';
import { useSelector } from 'react-redux';
import Skeleton from 'react-loading-skeleton';
import moment from 'moment';
import Divider from '../component/Divider';
import HorizontalScrollCard from '../component/HorizontalScrollCard';

const DetailPage = () => {
  const params = useParams();
  const imageURL = useSelector(state => state.movieoData.imageURL);
  
  // Fetching necessary data
  const { data } = useFetchDetail(`${params?.explore}/${params?.id}`);
  const { data: castData } = useFetchDetail(`${params?.explore}/${params?.id}/credits`);
  const { data: similarData } = useFetchDetail(`${params?.explore}/${params?.id}/similar`);
  const { data: videoData } = useFetchDetail(`${params?.explore}/${params?.id}/videos`);
  
  // Extracting the trailer information
  const trailer = videoData?.results?.find(video => video.type === 'Trailer');

  // Check if main data is loading
  if (!data) {
    return (
      <div>
        <Skeleton height={30} width={200} />
        <Skeleton count={3} />
      </div>
    );
  }

  // Calculate duration and get writer names
  const duration = (Number(data?.runtime) / 60).toFixed(1).split('.');
  const writer = castData?.crew?.filter(el => el?.job === 'Writer').map(el => el?.name).join(', ') || 'N/A';

  return (
    <div>
      {/* Backdrop Image */}
      <div className='w-full h-[280px] relative hidden lg:block'>
        <img
          src={`${imageURL}${data.backdrop_path}`}
          alt={data.title || data.name}
          className='w-full h-full object-cover'
        />
        <div className='absolute inset-0 bg-gradient-to-t from-neutral-900/90 to-transparent'></div>
      </div>

      {/* Main content section */}
      <div className='container mx-auto px-3 py-16 lg:py-1 flex flex-col lg:flex-row gap-5 lg:gap-10'>
        
        {/* Poster Image */}
        <div className='relative mx-auto lg:-mt-28 lg:mx-0 w-fit min-w-60'>
          <img
            src={`${imageURL}${data.poster_path}`}
            alt={data.title || data.name}
            className='w-60 h-80 object-cover rounded'
          />
        </div>

{/* Movie details */}
<div>
  <div className="flex justify-between items-center">
    <div>
      <h2 className='text-2xl lg:text-4xl font-bold text-white'>{data?.title || data?.name}</h2>
      <p className='text-neutral-400'>{data?.tagline}</p>
    </div>
    
    {/* Play Trailer Button */}
    <div className='ml-4'>
      {trailer ? (
        <a
          href={`https://www.youtube.com/watch?v=${trailer.key}`}
          target="_blank"
          rel="noopener noreferrer"
          className="bg-red-600 text-white py-2 px-4 rounded hover:bg-red-600 transition duration-300"
        >
          Play
        </a>
      ) : (
        <p className='text-neutral-400'>Trailer not available.</p>
      )}
    </div>
  </div>
  <Divider />
  
  {/* Ratings, Views, and Duration */}
  <div className='flex items-center gap-3'>
    <p>Rating: {Number(data.vote_average).toFixed(1)}+</p>
    <span className='text-white'>|</span>
    <p>Views: {Number(data.vote_count)}</p>
    <span className='text-white'>|</span>
    <p>Duration: {duration[0]}h {duration[1]}m</p>
  </div>
  <Divider />

          {/* Overview */}
          <div>
            <h3 className='text-xl font-bold'>Overview</h3>
            <p>{data.overview}</p>
            <Divider />

            {/* Additional Details */}
            <div className='flex items-center gap-3 my-3 text-center'>
              <p>Status: {data.status}</p>
              <span className='text-white'>|</span>
              <p>Release Date: {moment(data?.release_date).format('MMMM Do YYYY')}</p>
              <span className='text-white'>|</span>
              <p>Revenue: {Number(data.revenue).toLocaleString()}</p>
            </div>
            <Divider />
          </div>

          {/* Director and Writer */}
          <div>
            <p><span className='text-white'>Director</span>: {castData?.crew[0]?.name || 'N/A'}</p>
            <Divider />
            <p><span className='text-white'>Writer</span>: {writer}</p>
          </div>
        </div>
      </div>

      {/* Cast Section */}
      <Divider />
      <div className='container mx-auto px-3'>
        <h2 className='text-lg lg:text-2xl font-bold my-3 mx-0'>Star Cast</h2>
      </div>
      <div className='grid grid-cols-[repeat(auto-fit,96px)] gap-5 px-10'>
        {castData?.cast?.filter(el => el?.profile_path).map(starCast => (
          <div key={starCast.id} className="text-center">
            <img
              className='w-24 h-24 rounded-full object-cover'
              src={`${imageURL}${starCast.profile_path}`}
              alt={starCast.name}
            />
            <p className='font-bold text-sm text-neutral-400'>{starCast?.name}</p>
          </div>
        ))}
      </div>

      {/* Similar Movies Section */}
      <Divider />
      <h2 className='text-lg lg:text-2xl font-bold my-3'>Similar Movies</h2>
      {similarData?.results && similarData.results.length > 0 ? (
        <HorizontalScrollCard data={similarData.results} heading={`Similar ${params?.explore}`} />
      ) : (
        <p className='text-neutral-400'>No similar movies available.</p>
      )}
      
      {/* Loading State for Similar Movies */}
      {!similarData && <Skeleton count={3} height={200} />}
    </div>
  );
};

export default DetailPage;
